.body_card {
    background: #202124;
  height: 240px;
  width: 350px;
  border-radius: 20px 20px 20px 20px;
  margin-bottom: 20px;
  box-shadow: 1px 1px 5px #000000;
  overflow: hidden;
}

.body_cardEmpty {
  height: 240px;
  width: 350px;
  border-radius: 20px 20px 20px 20px;
  margin-bottom: 20px;
}

.body_card_picture {
    height: 70%;
}

.body_card_picture img{
    max-width: 100%;
    height:100%;
    object-fit: cover;
    border-bottom: black solid 2px;
    border-radius: 20px 20px 0px 0px;
}

.body_card_title {
    margin-left: 10px;
    margin-top: 10px;
}

.body_card_title h1{
    font-size: 18px;
    font-weight: bold;
    color:white;
}

.body_card_title h2{
    font-size: 12px;
    font-weight: bold;
    color:white;
}